@font-face {
  font-family: Cookie;
  src: url(font/CookieRunRegular.ttf);


} * {
  font-family: Cookie;

} body {
  text-align: center;
  background-color: #1e1f22;
/*313338,2a2c30,1e1f22*/
}.head, .body, .foot{
  background-color: #313338;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
  border: 1px solid #fff;
  color: #fff;

} .head {
  display: flow-root;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  height: 115px;

} .head h1 {
  margin-bottom: 5px;

} .head a {
  display: inline-flex;
  background-color: #2a2c30;
  margin-left: 40px;
  margin-right: 40px;
  border: 1px solid #fff;
  border-radius: 10px;
  width: 50px;
  height: 30px;
  flex-direction: column;
  justify-content: space-around;

} .body {
  border-radius: 10px;

} .foot {
  display: flow-root;
  justify-content: space-around;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 80px;
}




















/*//////////*/


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}